      text-align: center;
}
section.complete-profile .welcome-message button {
      width: auto;
}
section.complete-profile .process-form {
    padding: 48px;
    margin-bottom: 100px;
}
}
@media (min-width: 64em) {
section {
    height: calc(100% - 90px);
    padding-top: 0px;
    padding-bottom: 0px;
}
section.join-pool .box {
    width: 70%;
    margin-top: -100px;
}
section.profile div.right .box button {
    padding: 12px 50px;
}
}
